<template>
    <div class="product-grid">
        <article class="product-card" v-for="(product, index) in products" :key="index">
            <router-link :to="'products/' + product.content.slug">
                <Carousel :images="product.content.images" />
            </router-link>
            <router-link :to="'products/' + product.content.slug"><h1>{{ product.content.name }}</h1></router-link>
            <p>£{{ product.inventory.price.toFixed(2) }}</p>
            <button @click="addItemToCart(product)">Add to cart</button>
        </article>
    </div>
</template>

<script>
    import Carousel from "@/components/content-sections/Carousel.vue"

    export default {
        name: "product-grid",
        components: {
            Carousel
        },
        props: ["products"],
        methods: {
            addItemToCart(product) {
                this.$store.commit("addToCart", product)
            }
        }
    }
</script>

